import React from 'react';
import { Slide } from 'react-slideshow-image';
import mig1 from '../../img/migrant2.jpg'
import mig2 from '../../img/migrant3.jpg'
import mig3 from '../../img/migrant5.jpg'
import mig4 from '../../img/migrant6.jpg'
import mig5 from '../../img/migrant8.jpg'

function SliderImage2() {
    const slideImages = [
        process.env.PUBLIC_URL + '/images/migrant2.jpg',
        mig2,
        mig3,
        mig4,
        mig5
    ];

    const properties = {
        duration: 5000,
        transitionDuration: 500,
        infinite: true,
        indicators: true,
        arrows: true,
        pauseOnHover: true,
        onChange: (oldIndex, newIndex) => {
            console.log(`slide transition from ${oldIndex} to ${newIndex}`);
        }
    }
    return (
        <div className="slide-container">
            <Slide {...properties}>
                <div className="each-slide">
                    <div style={{ 'backgroundImage': `url(${slideImages[0]})` }}>
                        {/* <span>Slide 1</span> */}
                    </div>
                </div>
                <div className="each-slide">
                    <div style={{ 'backgroundImage': `url(${slideImages[1]})` }}>
                        {/* <span>Slide 2</span> */}
                    </div>
                </div>
                <div className="each-slide">
                    <div style={{ 'backgroundImage': `url(${slideImages[2]})` }}>
                        {/* <span>Slide 3</span> */}
                    </div>
                </div>
                <div className="each-slide">
                    <div style={{ 'backgroundImage': `url(${slideImages[3]})` }}>
                        {/* <span>Slide 3</span> */}
                    </div>
                </div>
                <div className="each-slide">
                    <div style={{ 'backgroundImage': `url(${slideImages[4]})` }}>
                        {/* <span>Slide 3</span> */}
                    </div>
                </div>
            </Slide>
        </div>

    )
}

export default SliderImage2;